ttom: 1px solid var(--grey-lighten-1, #BDBDBD);\n}\n.active-category[data-v-39a380dc] {\n    color: #151233 !important;\n    border-bottom: 2px solid !important\n}\n.organizations-actions[data-v-39a380dc] {\n    margin-top: 12px;\n    display: flex;\n    justify-content: space-between;\n    padding: 0 20px;\n}\n.search-input[data-v-39a380dc] {\n    width: 220px;\n    height: 32px;\n    color: #BCBCBC;\n    border-radius: 4px;\n    border: 1px solid var(--Grey, #BCBCBC);\n    padding: 8px 8px 8px 12px;\n}\n.invite-button[data-v-39a380dc] {\n    height: 32px;\n    padding: 6px 16px;\n    border-radius: 4px;\n    border: 1px solid var(--Plateau-Navy, #151233);\n    background-color: transparent;\n    line-height: initial;\n    color: var(--Plateau-Navy, #151233);\n    text-align: center;\n    font-size: 14px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n    text-transform: uppercase;\n}\n.respond-request-btn[data-v-39a380dc] {\n    border: none;\n    display: flex;\n    background: transparent;\n    color: #212121;\n    font-size: 14px;\n    font-size: 14px;\n    font-weight: 500;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/pages/organization/organizations.vue"],"names":[],"mappings":";AAqKA;IACI,qBAAqB;AACzB;AACA;IACI,gBAAgB;IAChB,eAAe;AACnB;AAEA;IACI,mBAAmB;IACnB,yBAAyB;IACzB,8CAA8C;IAC9C,mBAAmB;IACnB,WAAW;AACf;AAEA;IACI,iCAAiC;AACrC;AAEA;IACI,iCAAiC;IACjC,uBAAuB;AAC3B;AAEA;IACI,gCAAgC;IAChC,4BAA4B;AAChC;AAEA;IACI,gCAAgC;IAChC,4BAA4B;IAC5B,cAAc;IACd,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,mBAAmB;IACnB,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,iBAAiB;AACrB;AAEA;IACI,eAAe;IACf,iBAAiB;IACjB,kBAAkB;IAClB,cAAc;IACd,eAAe;AACnB;AAEA;IACI,wBAAwB;AAC5B;AAEA;IACI,mBAAmB;IACnB,eAAe;IACf,cAAc;IACd,yBAAyB;IACzB,0CAA0C;IAC1C,kBAAkB;IAClB,eAAe;IACf,iBAAiB;IACjB,kBAAkB;AACtB;AAEA;IACI,aAAa;AACjB;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,eAAe;IACf,YAAY;IACZ,eAAe;IACf,kBAAkB;AACtB;AAEA;IACI,yBAAyB;IACzB,eAAe;AACnB;AAEA;IACI,0BAA0B;IAC1B,YAAY;IACZ,eAAe;AAC