<IViewModel>(IViewModel);\nconst organizationGroupUseCase = container.resolve<IOrganizationGroupUseCase>(IOrganizationGroupUseCase);\nconst tableMethods = container.resolve<ITableMethods>(ITableMethods);\nconst organizations = ref<Array<IOrganizationData>>();\nconst orgGroupRequests = ref<Array<IGetOrganizationGroupReqResponse>>();\n\nconst selectedOrgGroup = ref(viewModel.user!.selectedOrganizationGroup);\nonBeforeMount(() => {\n    getOrganizationGroupDetails();\n    getOrganizationGroupRequests(\"joinOrganizationGroup\");\n})\nfunction getOrganizationGroupDetails(reloadPage?: boolean) {\n    executor.execute(async () => {\n        if (!checkGroupIsSet()) {\n            return\n        }\n        if(reloadPage) {\n            const organizationGroupId = selectedOrgGroup.value!.organizationGroup.ID;\n            const groupOrgs = (await organizationGroupUseCase.getOrganizationGroupDetails({organizationGroupId})).organizations;\n            organizations.value = groupOrgs.map(org => {\n                const organizationData: IOrganizationData = { ...org, showOrgInfo: false };\n                return organizationData;\n            });\n        }\n        else {\n            organizations.value = viewModel.user!.selectedOrganizationGroup!.organizations.map((org) => {\n                const organizationData: IOrganizationData = { ...org, showOrgInfo: false };\n                return organizationData;\n            });\n        }\n\n    }, { loading: true });\n}\nfunction getAppsByOrganizationId(orgId: string) {\n    if (!checkGroupIsSet()) {\n        return\n    }\n    const orgApps = organizationGroupUseCase.getOrganizationAppsByOrganizationIdForOrgGroup(orgId, viewModel.user!.selectedOrganizationGroup!);\n    return orgApps!;\n}\n\nasync function removeFromGroup(orgId: string) {\n    executor.execute(async () => {\n        await organizationGroupUseCase.deleteOrganizationFromGroup(orgId);\n        getOrganizationGroupDetails(true);\n    },{loading: true} )\n}\n\nfunction getAccountType(org: IOrganizationData) {\n    return org.accountType == \"premium\" ? \"Active\" : \"Inactive\";\n}\n\nfunction checkGroupIsSet() {\n    return viewModel.user?.selectedOrganizationGroup != undefined ? true : false\n}\n\nfunction getOrganizationGroupRequests(type: \"joinOrganizationGroup\") {\n    const organizationGroupID = viewModel.user!.selectedOrganizationGroup!.organizationGroup.ID;\n    executor.execute( async () => {\n        orgGroupRequests.value = await organizationGroupUseCase.getStudioRequests({type, organizationGroupID })\n\n    }, {loading: false})\n}\nfunction respondToRequest(requestId: string, accept: boolean) {\n    const organizationGroupID = viewModel.user!.selectedOrganizationGroup!.organizationGroup.ID;\n    executor.execute(async () => {\n        await organizationGroupUseCase.updateJoinOrgGroupRequest({accept, requestId, organizationGroupID});\n        getOrganizationGroupRequests('joinOrganizationGroup');\n    },{loading: true})\n}\n</script>\n<template>\n    <div>\n        <div class=\"header\">\n            <span class=\"header-text\">Organizations</span>\n            <span class=\"description-text\">Organizations description will be here, it can be much longer than this text\n                sample.</span>\n        </div>\n        <div class=\"organizations-tab\">\n            <div @click=\"activeTab = true; requestsTab = false\" :class=\"activeTab ? 'active-category' : ''\">\n                Active</div>\n            <div @click=\"requestsTab = true; activeTab = false;\" :class=\"requestsTab ? 'active-category' : ''\">\n                Requests</div>\n        </div>\n        <template v-if=\"activeTab\">\n            <div class=\"organizations-actions\">\n                <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"activeSearch\" />\n            </div>\n            <div class=\"table-div\">\n                <table class=\"organizations-table\">\n                    <tr class=\"thead\">\n                        <th class=\"col\">Organization</th>\n                        <th class=\"col\">Apps</th>\n                        <th class=\"col\">License</th>\n                        <th class=\"col\">Date Created</th>\n                        <th class=\"col-xl-1\"></th>\n                    </tr>\n                    <tr v-for=\"organization in organizations\" :key=\"organization.id\">\n                        <td><span>{{ organization.name }}</span></td>\n                        <td><span>{{ getAppsByOrganizationId(organization?.id)?.length }}</span></td>\n                        <td><span>{{ getAccountType(organization) }}</span></td>\n                        <td><span>{{ tableMethods.formatDate(organization.createDate) }}</span></td>\n                        <td>\n                            <div class=\"dropdown\">\n                                <i class=\"mdi mdi-dots-vertical organization-edit-menu-button\" id=\"dropdownEditMenuButton\"\n                                    data-bs-toggle=\"dropdown\" data-bs-target=\"#dropdown-menu\" aria-expanded=\"false\"></i>\n                                <ul class=\"dropdown-menu organization-edit-menu\" aria-labelledby=\"dropdownEditMenuButton\">\n                                    <li @click=\"removeFromGroup(organization.id)\" class=\"remove-from-organization\"><i\n                                            class=\"mdi mdi-delete-outline\"></i>Remove From Organization Group\n                                    </li>\n                                </ul>\n                            </div>\n                        </td>\n                    </tr>\n                </table>\n            </div>\n        </template>\n        <template v-else>\n            <div class=\"organizations-actions\">\n                <input class=\"search-input\" type=\"text\" placeholder=\"Search\" alt=\"search\" v-model=\"requestSearch\" />\n            </div>\n            <div class=\"table-div\">\n                <table class=\"organizations-table\">\n                    <tr class=\"thead\">\n                        <th class=\"col\">Organization</th>\n                        <th class=\"col\">Description</th>\n                        <th class=\"col\">Email Address</th>\n                        <th class=\"col\">Request Date</th>\n                        <th class=\"col-xl-1\"></th>\n                    </tr>\n                    <tr v-for=\"req in orgGroupRequests\" :key=\"req.id\">\n                        <td>{{ req.organizationName }}</td>\n                        <td>{{ req.description }}</td>\n                        <td>{{ req.requestingUserEmail }}</td>\n                        <td>{{ tableMethods.formatDate(req.requestDate) }}</td>\n                        <td style=\"display: flex;\">\n                            <button class=\"respond-request-btn\" @click=\"respondToRequest(req.id, true)\" style=\"margin-right: 20px;\">\n                                <i class=\"mdi mdi-check\" style=\"color: #29C660;margin-right: 4px;\"></i> Accept\n                            </button>\n                            <button class=\"respond-request-btn\" @click=\"respondToRequest(req.id, false)\">\n                                <i class=\"mdi mdi-close\" style=\"color: #FF6B6B;margin-right: 4px;\"></i> Reject\n                            </button>\n                        </td>\n                    </tr>\n                </table>\n            </div>\n        </template>\n    </div>\n</template>\n<style scoped>\n\n* {\n    font-family: 'Roboto';\n}\n.table-div {\n    margin-top: 12px;\n    padding: 0 20px;\n}\n\n.organizations-table {\n    background: #FFFFFF;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 1px 2px rgba(33, 33, 33, 0.16);\n    border-radius: 10px;\n    width: 100%;\n}\n\n.organizations-table td:hover {\n    background-color: none !important;\n}\n\n.organizations-table tbody>tr:hover {\n    background-color: none !important;\n    border: none !important;\n}\n\n.organizations-table th {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 17px 86px 17px 36px;\n}\n\n.organizations-table td {\n    border-bottom: 1px solid #E3E3E3;\n    padding: 13px 86px 13px 36px;\n    color: #212121;\n    font-size: 14px;\n    line-height: 16px;\n}\n\n.organizations-table .thead {\n    background: #F5F5F5;\n    color: #616161;\n    font-weight: 500;\n    font-size: 14px;\n    line-height: 16px;\n}\n\n.organization-edit-menu-button {\n    font-size: 24px;\n    line-height: 24px;\n    text-align: center;\n    color: #757575;\n    cursor: pointer;\n}\n\n.organization-edit-menu-button::after {\n    border: unset !important;\n}\n\n.organization-edit-menu {\n    background: #FFFFFF;\n    padding: 12px 0;\n    color: #212121;\n    border: 1px solid #E3E3E3;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    border-radius: 8px;\n    font-size: 14px;\n    line-height: 16px;\n    width: max-content;\n}\n\n.organization-edit-menu li {\n    padding: 10px;\n}\n\n.organization-edit-menu span {\n    display: flex;\n    align-items: center;\n    cursor: pointer;\n    height: 30px;\n    padding: 0 12px;\n    position: relative;\n}\n\n.organization-edit-menu li:hover {\n    background-color: #d5d5d5;\n    cursor: pointer;\n}\n\n.organization-edit-menu li.disabled {\n    /* pointer-events: none; */\n    opacity: 0.5;\n    cursor: default;\n}\n\n.organization-edit-menu:has(.remove-from-organization:hover) .remove-from-organization-tooltip,\n.organization-edit-menu:has(.remove-owner:hover) .remove-owner-tooltip {\n    visibility: visible;\n}\n\n.organization-edit-menu .remove-from-organization-tooltip,\n.organization-edit-menu .remove-owner-tooltip {\n    position: absolute;\n    top: -60px;\n    padding: 10px;\n    background-color: #757575;\n    color: #FFF;\n    height: auto;\n    font-size: 12px;\n    border-radius: 4px;\n    box-shadow: 1px 2px 4px 0px rgba(0, 0, 0, 0.20);\n    visibility: hidden;\n}\n\n.organization-edit-menu .remove-owner-tooltip {\n    top: 12px;\n}\n\n.organization-edit-menu span.disabled:hover::after {\n    visibility: visible;\n}\n\n.organization-edit-menu .disabled:hover .menu_tooltip {\n    visibility: visible;\n}\n\n.description-text {\n    color: var(--Grey-darken-3, #616161);\n    font-size: 16px;\n    font-style: normal;\n    font-weight: 400;\n    line-height: normal;\n    margin-top: 8px;\n}\n\n.header {\n    border-bottom: 1px solid var(--Grey-lighten-3, #E3E3E3);\n    display: flex;\n    padding: 20px;\n    flex-direction: column;\n}\n\n.header-text {\n    color: var(--Plateau-Navy, #151233);\n    font-size: 20px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n    text-transform: capitalize;\n}\n\n.organizations-tab div {\n    color: #A1A1A1;\n    font-weight: 500;\n    font-size: 14px;\n    width: 282px;\n    padding-bottom: 8px;\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    cursor: pointer;\n}\n\n.organizations-tab {\n    display: flex;\n    align-items: center;\n    margin: 12px 20px 0;\n    border-bottom: 1px solid var(--grey-lighten-1, #BDBDBD);\n}\n\n.active-category {\n    color: #151233 !important;\n    border-bottom: 2px solid !important\n}\n\n.organizations-actions {\n    margin-top: 12px;\n    display: flex;\n    justify-content: space-between;\n    padding: 0 20px;\n}\n\n.search-input {\n    width: 220px;\n    height: 32px;\n    color: #BCBCBC;\n    border-radius: 4px;\n    border: 1px solid var(--Grey, #BCBCBC);\n    padding: 8px 8px 8px 12px;\n}\n\n.invite-button {\n    height: 32px;\n    padding: 6px 16px;\n    border-radius: 4px;\n    border: 1px solid var(--Plateau-Navy, #151233);\n    background-color: transparent;\n    line-height: initial;\n    color: var(--Plateau-Navy, #151233);\n    text-align: center;\n    font-size: 14px;\n    font-style: normal;\n    font-weight: 500;\n    line-height: normal;\n    text-transform: uppercase;\n}\n.respond-request-btn {\n    border: none;\n    display: flex;\n    background: transparent;\n    color: #212121;\n    font-size: 14px;\n    font-size: 14px;\n    font-weight: 500;\n}\n</style>"],"sourceRoot":""}]);
